<template>
    <views class="person">
        <nav-bar
            :title="moduleTitle"
            :left-arrow="false"
            fixed>
        </nav-bar>
        <user-info></user-info>
        <views class="person-menus">
            <person-item
                v-for="item in list"
                :key="item.title"
                :title="item.title"
                :icon="item.icon"
                @click="handleClick(item)">
            </person-item>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import UserInfo from '@/components/userInfo'
import PersonItem from './PersonItem'
import tools from '@/mixins/tools'
export default {
    name: 'Person',
    mixins: [tools],
    components: {
        NavBar,
        UserInfo,
        PersonItem
    },
    data() {
        return {
            list: [
                {
                    title: '中奖消息',
                    icon: require('@/assets/images/person01.png'),
                    url: '/gift-list',
                    tip: false
                },
                {
                    title: '我的积分',
                    icon: require('@/assets/images/person02.png'),
                    url: '/point',
                    tip: false
                },
                {
                    title: '完善资料',
                    icon: require('@/assets/images/person03.png'),
                    url: '/improve-info',
                    tip: false
                },
                {
                    title: '我的收藏',
                    icon: require('@/assets/images/person04.png'),
                    url: '/collected',
                    tip: false
                },
                {
                    title: '追踪溯源',
                    icon: require('@/assets/images/person05.png'),
                    url: '/verify?type=1&trace=1',
                    tip: false
                },
                {
                    title: '附近门店',
                    icon: require('@/assets/images/person06.png'),
                    url: '/shop',
                    tip: false
                },
                {
                    title: '常见问题',
                    icon: require('@/assets/images/person07.png'),
                    url: '/faq',
                    tip: false
                },
                {
                    title: '反馈意见',
                    icon: require('@/assets/images/person08.png'),
                    url: '/feedback',
                    tip: false
                },
                {
                    title: '联系我们',
                    icon: require('@/assets/images/person09.png'),
                    url: '/contact',
                    tip: false
                }
            ]
        }
    },
    computed: {
        moduleTitle() {
            return '个人中心'
        }
    },
    mounted() {
        this.setBgColor()
    },
    destroyed() {
        this.resetBgColor()
    },
    activated() {
        this.setBgColor()
    },
    deactivated() {
        this.resetBgColor()
    },
    methods: {
        handleClick(item) {
            this.push(item.url)
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
